/* ------------------------------------------------------------ *\
	Splash Screen
\* ------------------------------------------------------------ */

.splash,
.splash-image { @extend centered; }

.splash { text-align: center; flex: 0 0 100%; display: flex; flex-flow: row wrap; align-items: flex-end; justify-content: center; opacity: 1; position: fixed; z-index: 10; padding-bottom: 6px; transition: all .5s ease-out; }
.splash .btn { opacity: 0; position: relative; z-index: 2; border-radius: 50px; transition: opacity .5s ease 3s; }

.splash-image { flex: 0 0 100%; z-index: 1; transform: scale(1); transition: transform 20s ease-in-out; }

.splash-logo { flex: 0 0 185px; opacity: 0; position: absolute; top: 50%; width: 185px; left: 50%; z-index: 2; transform: translate(-50%, -50%); transition: opacity 3s ease-in-out, transform 3s ease-in-out; }
.splash-logo img { width: 100%; height: auto; display: block; margin: auto; }

.splash-logo .splash-logo-letter { width: 30px; position: absolute; top: 1px; left: 30px; }

$breakpoint-tablet {
	.splash-logo { width: 415px; flex: 0 0 415px; }
	.splash-logo .splash-logo-letter { width: 66px; left: 70px; }
}
