/* ------------------------------------------------------------ *\
	Table History
\* ------------------------------------------------------------ */

.table-history { font-size: 11px; line-height: 1.25; padding: 8px 0; margin: 0; }

.table-history a,
.table-history tr,
.table-history th,
.table-history td,
.table-history table,
.table-history thead,
.table-history tbody,
.table-history tfoot { display: block; }

.table-history tr { position: relative; padding: 16px 0; border-bottom: 1px dotted transparent; background: url(images/dotted.png) repeat-x 0 100% / auto 1px; }
.table-history td { padding: 0; border: 0; }
.table-history table { margin: 0; }

.table-history td:first-child { padding-right: 50px; }
.table-history td:last-child { position: absolute; top: 50%; right: 0; margin-top: -8px; }

.table-history td:nth-child(2),
.table-history td:nth-child(3) { display: inline-block; }

.table-history td:nth-child(2) span:after { content: ' - '; }

.table-history span { font-size: 10px; color: $text-faded; display: block; padding-top: 2px; }
.table-history span.withdraw { font-size: 11px; color: #dc0000; }
.table-history span.deposit { font-size: 11px; color: #00b828; }

$breakpoint-tablet {
	.table-history { font-size: 15px; padding: 4px 0; }
	.table-history a { padding: 24px 0; }

	.table-history th,
	.table-history td,
	.table-history td:nth-child(2),
	.table-history td:nth-child(3) { display: table-cell; }

	.table-history td { width: 13.5%; padding: 0; border-bottom: 1px dotted transparent; background: url(images/dotted.png) repeat-x 0 100% / auto 1px; }
	.table-history td:first-child { width: 58%; padding-right: 0; }
	.table-history td:last-child { text-align: right; position: static; top: auto; right: auto; margin-top: 0; }

	.table-history td:nth-child(2) { width: 19%; }
	.table-history td:nth-child(2) span:after { content: none; }
	
	.table-history tr { display: table-row; position: static; padding: 0; border: 0; }
	.table-history table { display: table; }
	.table-history thead { display: table-header-group; }
	.table-history tbody { display: table-row-group; }
	.table-history tfoot { display: table-footer-group; }

	.table-history span { font-size: 13px; padding-top: 0; }
	.table-history span small { font-size: 100%; }

	.table-history span.withdraw,
	.table-history span.deposit { font-size: 15px; }
}
