/* ------------------------------------------------------------ *\
	Base
\* ------------------------------------------------------------ */

html,
body { height: 100%; }

body { font-family: $font-family-sans-serif; font-size: $font-size-base; line-height: $line-height-base; color: $text; min-width: 320px; background: $white url(images/temp/waves-mobile.jpg) no-repeat 50% 0 / 100% auto fixed; display: flex; }

a,
a[href^="tel"] { color: inherit; text-decoration: none; }

a,
input,
select,
button,
textarea,
.btn { transition: color .35s ease-in-out, opacity .35s ease-in-out, border-color .35s ease-in-out, background-color .35s ease-in-out, box-shadow .35s ease-in-out; }

h1 { font-size: 16px; line-height: 1.25; color: $highlight; font-weight: 400; letter-spacing: 0; }

$breakpoint-tablet {
	body { background-image: url(images/temp/waves.jpg); }

	h1 { font-size: 24px; }
}

@media ( min-width: 1025px ) {
	a:hover { color: inherit; text-decoration: none; }
}
