/* ------------------------------------------------------------ *\
	Form Currency
\* ------------------------------------------------------------ */

.form-currency { padding-bottom: 20px; }
.form-currency .btn { width: 100%; display: block; }

.form-currency form,
.form-currency .btn { margin: 0; }

.form-currency .form-head { font-size: 12px; line-height: 1.51; color: $text-faded; font-weight: 300; text-align: center; max-width: 583px; margin: auto; }
.form-currency .form-head h1 { margin-bottom: 5px; }
.form-currency .form-head p { margin-bottom: 22px; }
.form-currency .form-head i { margin: 0 auto 25px; transform: scale(0.9) rotate(3deg) translateX(6px); }

.form-currency .form-body { max-width: 331px; margin: auto; }
.form-currency .form-icon { animation-delay: 3s; transform: translate(6px, 0%); }

.form-currency .form-actions { max-width: 278px; padding-top: 95px; margin: auto; }

$breakpoint-tablet {
	.form-currency { padding-bottom: 5.07812vh; }

	.form-currency .form-head { font-size: 14px; line-height: 1.58; }
	.form-currency .form-head h1 { margin-bottom: 7px; }
	.form-currency .form-head i { margin-bottom: 64px; transform: scale(1) rotate(0.5deg) translateX(-6px); }

	.form-currency .form-body { padding-top: 6px; }
	.form-currency .form-actions { padding-top: 19.922vh; }
}

$breakpoint-small {
	.form-currency { padding-bottom: 4vh; }
	.form-currency .form-actions { padding-top: 5vh; }
}

$breakpoint-large {
	.form-currency .form-actions { padding-top: 20.3vh; }
}

$breakpoint-minor {
	.form-currency .form-actions { padding-top: 5vh; }
}

/* ------------------------------------------------------------ *\
	Form Funds
\* ------------------------------------------------------------ */

.form-funds { padding-bottom: 5px; }

.form-funds .form-row { position: relative; margin-bottom: 15px; }
.form-funds .form-label { font-size: 10px; line-height: 1; color: $text-faded; font-weight: 400; opacity: 0; position: absolute; top: 0; left: 0; transform: translateY(100%); transition: all .25s ease-out; }
.form-funds .form-controls { position: relative; }

.form-funds .form-actions { padding-top: 17px; }
.form-funds .form-actions .btn { width: 100%; }

.form-funds .form-controls-set .field { font-size: 0; line-height: 0; }
.form-funds .form-controls-set .field::placeholder { opacity: 0; }

.form-funds .form-currency,
.form-funds .form-currency-mark { font-size: 16px; line-height: 18px; color: $text; font-weight: 300; text-transform: uppercase; position: absolute; top: 0; padding: 12px 0; pointer-events: none; }

.form-funds .form-currency { right: 0; }
.form-funds .form-currency-mark { font-weight: 400; display: none; left: 0; padding-top: 11px; }

.form-funds .form-head { font-size: 12px; line-height: 1.51; color: $text-faded; font-weight: 300; text-align: center; padding-bottom: 8px; }
.form-funds .form-head h1 { margin-bottom: 5px; }
.form-funds .form-head p { margin-bottom: 22px; }
.form-funds .form-head i { margin: 0 auto 25px; transform: scale(0.9) rotate(-6deg) translate(6px, 3px); }

.form-funds .form-row-icon,
.form-funds .form-row-icon:before,
.form-funds .form-row-icon:after { position: absolute; top: 0; right: 0; bottom: 0; margin: auto; pointer-events: none; }

.form-funds .form-row-icon-dots,
.form-funds .form-row-icon-dots:before,
.form-funds .form-row-icon-dots:after { content: ''; width: 6px; height: 6px; background-color: $text-action; border-radius: 50%; }

.form-funds .form-row-icon-dots { right: 10px; }
.form-funds .form-row-icon-dots:before { right: -10px; }
.form-funds .form-row-icon-dots:after { left: -20px; }

.form-funds .form-field-value { font-family: $font-family-sans-serif; font-size: 16px; line-height: 18px; color: $text; font-weight: 400; white-space: nowrap; text-overflow: ellipsis; position: absolute; bottom: 11px; left: 0; right: 40px; overflow: hidden; pointer-events: none;}
.form-funds .form-field-value strong { font-weight: 400; }
.form-funds .form-field-value small { font-size: 14px; color: $text-faded; }

.form-funds input[type="number"], 
.form-funds input[type="text"] { padding-right: 40px; }

.form-funds input[type="number"]:valid { text-indent: 10px; }
.form-funds input[type="number"]:valid ~ .form-currency-mark { display: block; }

.form-funds .form-controls-set .form-label,
.form-funds input[type="number"]:valid ~ .form-label { opacity: 1; transform: translateY(0%); }

/*  Form Funds Withdraw  */

.form-funds-withdraw { padding-bottom: 0; }
.form-funds-withdraw form { margin-bottom: 20px; }

.form-funds-withdraw .btn { margin-bottom: 0; }

$breakpoint-tablet {
	.form-funds { padding-bottom: 17px; }

	.form-funds .form-head { font-size: 14px; line-height: 1.58; max-width: 610px; margin: auto; }
	.form-funds .form-head h1 { margin-bottom: 7px; }
	.form-funds .form-head i { margin-bottom: 66px; transform: scale(1) rotate(0.5deg) translate(0, 0); }

	.form-funds .form-body { max-width: 400px; padding-top: 2px; margin: auto; }
	.form-funds .form-label { font-size: 12px; top: -5px; }
	.form-funds .form-actions { max-width: 278px; padding-top: 13.3789vh; margin: auto; }

	.form-funds .form-currency,
	.form-funds .form-currency-mark { font-size: 14px; line-height: 18px; padding-top: 11px; }

	.form-funds .form-field-value { font-size: 14px; line-height: 18px; }
	.form-funds .form-field-value small { font-size: 12px; }

	.form-funds-withdraw { padding-bottom: 32px; }
}

$breakpoint-small {
	.form-funds { padding-bottom: 2vh; }
	.form-funds form { margin-bottom: 1vh; }
	
	.form-funds .form-actions { padding-top: 3vh; }

	.form-funds .form-head { padding-bottom: 0; }
	.form-funds .form-head p { margin-bottom: 1.5vh; }
}

$breakpoint-minor {
	.form-funds { padding: 0 0 1vh; }
	.form-funds form { margin-bottom: 0; }
	.form-funds .form-head { padding: 0; }
	.form-funds .form-actions { padding-top: 0; }
}
