/* ------------------------------------------------------------ *\
	Widget
\* ------------------------------------------------------------ */

.widgets { list-style: none outside none; }
.widgets:after { @extend clearfix; }

/* ------------------------------------------------------------ *\
	Widget
\* ------------------------------------------------------------ */

.widget-head { background-color: #dedede; padding: 11px 18px 10px; margin: 0 -20px; }
.widget-head h2 { font-size: 11px; line-height: 13px; color: #000; font-weight: 500; letter-spacing: 0.02em; margin: 0; }

$breakpoint-tablet {
	.widget-head { padding: 12px 0; border-bottom: 1px solid #dedede; margin: 0; background-color: transparent; }
	.widget-head h2 { font-size: 13px; line-height: 16px; opacity: .65; }
}
