/* ------------------------------------------------------------ *\
	Nav
\* ------------------------------------------------------------ */

.nav { @extend centered; position: fixed; z-index: 4; background-color: color($black a(0%)); transition: all .35s ease-in-out; pointer-events: none; }

.nav-head { display: flex; flex-flow: row nowrap; align-items: center; justify-content: space-between; padding: 17px 20px 13px; border-bottom: 1px solid color($highlight a(5%)); }
.nav-head .btn { position: relative; top: 3px; }

.nav-container { width: 241px; height: 100%; transform: translateX(-100%); transition: transform .35s ease-in-out; }

.nav-user { font-size: 10px; color: $highlight; font-weight: 400; }
.nav-user a { display: block; }
.nav-user a i { margin-left: 4px; }
.nav-user strong { font-weight: 400; }

.nav-body { font-size: 12px; line-height: 1.5; color: $highlight; font-weight: 400; padding: 10px 0; }
.nav-body ul { padding: 0; margin: 0; }
.nav-body li { margin: 0; }
.nav-body a { display: block; padding: 8px 20px 9px; }

.nav-foot { font-size: 14px; line-height: 1.25; color: $highlight; position: absolute; bottom: 51px; right: 60px; left: 60px; }
.nav-foot a { display: block; }
.nav-foot a i { float: right; }

.nav-visible { background-color: color($black a(20%)); pointer-events: auto; }
.nav-visible .nav-container { transform: translateX(0%); }

$breakpoint-tablet {
	.nav-container { width: 400px; position: relative; }

	.nav-head { padding: 50px 62px 42px 60px; }
	.nav-body { font-size: 14px; line-height: 1.35; padding: 52px 40px; }

	.nav-user { font-size: 14px; }
	.nav-user a i { margin-left: 6px; }
}
