/* ------------------------------------------------------------ *\
	Helpers
\* ------------------------------------------------------------ */

/*  Clear  */

.clear:after { content: ''; line-height: 0; display: table; clear: both; }

/*  Notext  */

.notext { white-space: nowrap; text-indent: 100%; text-shadow: none; overflow: hidden; }
.notext-old { font-size: 0; line-height: 0; text-indent: -4000px; }

/*  Hidden  */

[hidden],
.hidden { display: none; }

/*  Alignleft  */

.alignleft { float: left; }

/*  Alignright  */

.alignright { float: right; }

/*  Disabled  */

[disabled],
.disabled { cursor: default; }

/*  Fullsize Background  */

.fullsize-background { background-repeat: no-repeat; background-position: 50% 50%; background-size: cover; }

@media ( max-width: $responsive-sm ) {
	.desktop-only { display: none; }
}

$breakpoint-tablet {
	.mobile-only { display: none; }
}

@keyframes kenburns {
	0% { opacity: 0; }
	5% { opacity: 1; }
	95% { transform: scale3d(1.5, 1.5, 1.5) translate3d(-190px, -120px, 0px); animation-timing-function: ease-in; opacity: 1; }
	100% { transform: scale3d(1.5, 1.5, 1.5) translate3d(-170px, -100px, 0px); animation-play-state: paused; }
}

@keyframes rotate-in {
	from {
		transform: translateY(100%) rotate(45deg);
	}

	to {
		transform: translateY(0%) rotate(0deg);
	}
}

@keyframes rotate-out {
	from {
		transform: translateY(0%) rotate(0deg);
	}

	to {
		transform: translateY(-100%) rotate(0deg);
	}
}

@keyframes float {
	0% {
		transform: rotate(0deg) translate(0px, 0vh);
	}

	50%, 100% {
		transform: rotate(3deg) translate(12px, 0vh);
	}

	25%, 75% {
		transform: rotate(-3deg) translate(-12px, 0vh)
	}
}

@keyframes rise {
	from {
		transform: translate(-50%, -50%);
	}

	to {
		transform: translate(-50%, -200%);
	}
}
