/* ------------------------------------------------------------ *\
	Section
\* ------------------------------------------------------------ */

.section .section-title { font-size: 11px; line-height: 13px; color: #000; font-weight: 500; letter-spacing: 0.02em; float: left; padding: 6px 0 0; margin: 0; }
.section .section-title a { font-size: 10px; line-height: 11px; color: $white; font-weight: 500; text-transform: uppercase; float: right; padding: 5px 10px; margin: -4px 0; background-color: color($black a(50%)); border-radius: 10px; }
.section .section-title small { font-size: 100%; font-weight: 300; padding-left: 2px; }
.section .section-title strong { text-transform: uppercase; font-weight: 500; float: right; }

.section .section-head { padding: 6px 0 7px; background-color: #dedede; }

.section .section-head-actions { float: right; }
.section .section-head-actions span { font-size: 13px; line-height: 14px; color: #9da8b1; font-weight: 400; letter-spacing: 0.025em; float: left; padding: 5px; }
.section .section-head-actions .btn { float: left; margin-left: 4px; }

.section .section-head-primary { color: $white; padding: 14px 0 12px; background-color: $highlight; }
.section .section-head-primary h3 { font-weight: 500; }
.section .section-head-primary h4 { font-weight: 300; }

.section .section-head-primary h3,
.section .section-head-primary h4 { font-size: 11px; line-height: 1.4; letter-spacing: 0.03em; margin: 0; }

.section .section-head-primary h3 strong,
.section .section-head-primary h4 strong { font-weight: inherit; float: right; }

.section .section-head-primary-reverse h3,
.section .section-head-primary-reverse h4 { font-weight: 500; }

.section .section-head-primary-reverse h3 strong,
.section .section-head-primary-reverse h4 strong { font-weight: 300; display: block; float: none; }

.section .section-head-primary-reverse h3 { float: left; }
.section .section-head-primary-reverse h4 { float: right; text-align: right; }

.section .section-head-secondary { padding: 6px 0 9px; background-color: #b3d900; }
.section .section-head-secondary .section-title { float: none; margin-left: -2px; }

.section .section-balloons-scrollable,
.section .section-transactions-scrollable { overflow-x: hidden; overflow-y: auto; -webkit-overflow-scrolling: touch; }

.section .section-balloons-scrollable { height: calc(100vh - 360px); }
.section .section-transactions-scrollable { height: calc(100vh - 326px); }

$breakpoint-tablet {
	.section .section-title { font-size: 15px; }
	.section .section-title a { font-size: 12px; line-height: 14px; padding: 5px 17px; margin-top: -5px; border-radius: 12px; }

	.section .section-head { padding: 10px 0; }

	.section .section-head-actions .btn { margin-left: 11px; }
	.section .section-head-actions .btn:not(.current) { background-color: #6f6f6f; }
	.section .section-head-actions .btn:not(.current):hover { background-color: #b3d900; }

	.section .section-head-primary { padding: 19px 0; }

	.section .section-head-primary h3,
	.section .section-head-primary h4 { font-size: 15px; line-height: 17px; }

	.section .section-head-primary h3 strong,
	.section .section-head-primary h4 strong { float: none; }

	.section .section-head-primary h3 { float: left; letter-spacing: 0.04em; }
	.section .section-head-primary h4 { float: right; letter-spacing: 0; }

	.section .section-head-primary-reverse h3,
	.section .section-head-primary-reverse h4 { font-weight: 500; letter-spacing: 0.02em; }

	.section .section-head-primary-reverse h3 strong,
	.section .section-head-primary-reverse h4 strong { font-weight: 300; display: inline; }

	.section .section-head-secondary { padding: 9px 0 14px; }

	.section .section-head-secondary .section-title { margin-left: 0; }
	.section .section-head-secondary .section-title small { font-size: 13px; padding-left: 4px; }
	.section .section-head-secondary .section-title strong { font-size: 12px; line-height: 1; padding-top: 3px; }

	.section .section-balloons-scrollable,
	.section .section-transactions-scrollable { height: auto; overflow: visible; }

	.section .widgets { padding-top: 23px; margin: 0 -19px; }
	.section .widget { width: 50%; float: left; padding: 0 19px; }
	.section .widget-wide { width: 100%; margin-top: -17px; }
}

/* ------------------------------------------------------------ *\
	Section Funds
\* ------------------------------------------------------------ */

.section-funds { text-align: center; }
.section-funds h1 { margin-bottom: 5px; }

.section-funds .btn { width: 100%; display: block; margin-bottom: 40px; }

.section-funds .section-head { font-size: 12px; line-height: 1.51; color: $text-faded; padding-bottom: 85px; }
.section-funds .section-head p { max-width: 210px; margin: auto; }

$breakpoint-tablet {
	.section-funds { padding-bottom: 9.9609vh; }
	.section-funds h1 { margin-bottom: 7px; }

	.section-funds .btn { min-width: 280px; width: auto; float: left; }
	.section-funds .btn + .btn { float: right; }

	.section-funds .section-head { font-size: 14px; line-height: 1.58; padding-bottom: 8.30078vh; }
	.section-funds .section-head p { max-width: 600px; }
	.section-funds .section-head i { margin-bottom: 65px; }

	.section-funds .section-actions { max-width: 600px; padding-top: 12.3047vh; margin: auto; }
	.section-funds .section-actions:after { content: ''; line-height: 0; display: table; clear: both; }
}

$breakpoint-small {
	.section-funds { padding-bottom: 5vh; }
	.section-funds .section-actions { padding-top: 2vh; }
}

$breakpoint-large {
	.section-funds .section-actions { padding-top: 12.9vh; }
}

$breakpoint-minor {
	.section-funds .btn { margin-bottom: 20px; }
	.section-funds .section-head { padding-bottom: 5vh; }
}

/* ------------------------------------------------------------ *\
	Section Review
\* ------------------------------------------------------------ */

.section-review { padding-bottom: 40px; }
.section-review h1 { margin-top: 22px; }
.section-review .btn { width: 100%; }

.section-review .section-head { text-align: center; }
.section-review .section-body { padding-top: 44px; }
.section-review .section-foot { padding-top: 47px; }

/*  Section Review Withdraw  */

.section-review-withdraw { padding-bottom: 20px; }

.section-review-withdraw .list-funds li { padding: 21px 0 22px 48px; }
.section-review-withdraw .list-funds li em { padding-top: 2px; }
.section-review-withdraw .list-funds li strong { max-width: none; }
.section-review-withdraw .list-funds li:first-child span { text-transform: lowercase; background-color: $highlight; }
.section-review-withdraw .list-funds li:last-child span { text-transform: uppercase; background-color: #653394; }

.section-review-withdraw .section-body { padding-top: 23px; }
.section-review-withdraw .section-foot { padding-top: 30px; }

$breakpoint-tablet {
	.section-review { padding-bottom: 5.07812vh; }
	.section-review .section-head i { margin-bottom: 13.28129vh; }
	.section-review .section-body { max-width: 400px; padding-top: 10px; margin: auto; }
	.section-review .section-foot { max-width: 280px; padding-top: 14.0625vh; margin: auto; }

	.section-review-withdraw .list-funds li { padding: 15px 0 15px 71px; }
	.section-review-withdraw .list-funds li em { padding-top: 9px; }

	.section-review-withdraw .section-foot { padding-top: 7.95vh; }
}

$breakpoint-small {
	.section-review { padding-bottom: 5vh; }
	.section-review .section-foot { padding-top: 5.5vh; }
	.section-review .section-head i { margin-bottom: 4vh; }

	.section-review-withdraw { padding-bottom: 4vh; }
	.section-review-withdraw .section-foot { padding-top: 4vh; }
	.section-review-withdraw .section-body { padding-top: 1vh; }

	.section-review-withdraw .section-head h1 { margin-bottom: 2vh; }
	.section-review-withdraw .section-head i { margin-bottom: 2vh; }
}

$breakpoint-large {
	.section-review .section-foot { padding-top: 14.2vh; }

	.section-review-withdraw .section-foot { padding-top: 7.8vh; }
}

$breakpoint-minor {
	.section-review { padding-bottom: 4vh; }
	.section-review .section-body { padding-top: 1vh; }
	.section-review .section-foot { padding-top: 4vh; }

	.section-review-withdraw .list-funds li { padding: 11px 0 11px 48px; }
}

/* ------------------------------------------------------------ *\
	Section Launch
\* ------------------------------------------------------------ */

.section-launch { padding-bottom: 20px; }
.section-launch h1 { margin: 0 0 5px; }
.section-launch .section-head { font-size: 12px; line-height: 1.51; color: $text-faded; }

.section-launch .section-foot { position: relative; padding-top: 62px; }
.section-launch .section-foot i.mobile-only { display: block; margin: 0 auto -18px; }

$breakpoint-tablet {
	.section-launch { padding-bottom: 52px; }
	.section-launch h1 { margin-bottom: 9px; }
	.section-launch .section-head { font-size: 14px; max-width: 610px; margin: auto; }
	
	.section-launch .section-foot { padding-top: 50px; }
	.section-launch .section-foot i.mobile-only { display: none; }
	.section-launch .section-foot i.desktop-only { display: block; margin: 0 auto -66px; transform: scale(.75) rotate(-3deg) translateX(3px); }
}

$breakpoint-small {
	.section-launch { padding-bottom: 6vh; }
	.section-launch .section-foot { padding-top: 2vh; }
}

$breakpoint-minor {
	.section-launch .section-foot { padding-top: 5vh; }
}

/* ------------------------------------------------------------ *\
	Section Launched
\* ------------------------------------------------------------ */

.section-launched .section-head { position: relative; }
.section-launched .section-head i { position: absolute; bottom: 100%; left: 50%; transform: scale(.62) translate(-76%, 24px) rotate(-3deg); }

.section-launched .section-foot { padding-top: 75px; }

$breakpoint-small {
	.section-launched .section-foot { padding-top: 7.25vh; }

	.section-launched .section-head i { position: absolute; bottom: 100%; left: 50%; transform: scale(1) translate(-50%, -4vh) rotate(0deg); }
}

$breakpoint-large {
	.section-launched .section-foot { padding-top: 25.6vh; }

	.section-launched .section-head i { position: absolute; bottom: 100%; left: 50%; transform: scale(1) translate(-50%, 7vh) rotate(0deg); }
}

$breakpoint-minor {
	.section-launched .section-foot { padding-top: 2vh; }
}

/* ------------------------------------------------------------ *\
	Section Pop
\* ------------------------------------------------------------ */

.section-pop h1 { margin: 0 0 5px; }

.section-pop .section-head { font-size: 12px; line-height: 1.51; color: $text-faded; }
.section-pop .section-head i { margin-bottom: 65px; transform: scale(1.6);}

.section-pop .section-foot { padding-top: 56px; }

$breakpoint-tablet {
	.section-pop h1 { margin-bottom: 9px; }

	.section-pop .section-head { font-size: 14px; max-width: 580px; margin: auto; }
	.section-pop .section-head i { transform: scale(1); margin-bottom: 6.34766vh; }

	.section-pop .section-foot { padding-top: 17.5781vh; }
}

$breakpoint-small {
	.section-pop .section-foot { padding-top: 5vh; }
}

$breakpoint-large {
	.section-pop .section-foot { padding-top: 18vh; }
}

$breakpoint-minor {
	.section-pop .section-foot { padding-top: 0; }
}

/* ------------------------------------------------------------ *\
	Section Popped
\* ------------------------------------------------------------ */

.section-popped { padding-bottom: 40px; }
.section-popped h1 { text-align: center; margin: 0 0 13px; }
.section-popped h2 { font-size: 16px; line-height: 1.25; color: $highlight; font-weight: 400; letter-spacing: 0; text-align: center; margin: 0 0 9px; }

.section-popped .btn { width: 100%; display: block; }

.section-popped .socials { text-align: center; }
.section-popped .socials a { margin: 0 5px; }

.section-popped .section-head { text-align: center; }
.section-popped .section-head i { margin-bottom: 24px; }

.section-popped .section-foot { padding-top: 24px; }

$breakpoint-tablet {
	.section-popped { padding-bottom: 5.07812vh; }
	.section-popped h1 { margin-bottom: 20px; }
	.section-popped h2 { font-size: 24px; margin: 8px 0 13px; }

	.section-popped .socials a { margin: 0 7px; }

	.section-popped .section-head i { margin-bottom: 6.64063vh; }

	.section-popped .section-body { max-width: 400px; margin: auto; }
	.section-popped .section-foot { max-width: 280px; padding-top: 3.8086vh; margin: auto; }
}

$breakpoint-small {
	.section-popped { padding-bottom: 3vh; }
	
	.section-popped .list-balloon-details { padding-bottom: .5vh; }
	.section-popped .list-balloon-details li { padding: 1.5vh 0; }

	.section-popped .section-foot { padding-top: 2vh; }
	.section-popped .section-head i { margin-bottom: 3vh; }
}

$breakpoint-minor {
	.section-popped { padding-bottom: 3vh; }
	.section-popped .section-foot { padding-top: 3vh; }
	.section-popped .section-head i { margin-bottom: 3vh; }

	.section-popped .list-balloon-details { padding-bottom: 2.5vh; }
	.section-popped .list-balloon-details li { padding: 5px 0; }
}

/* ------------------------------------------------------------ *\
	Section Primary
\* ------------------------------------------------------------ */

.section-primary .section-head { text-align: center; padding: 6px 0 4px; }
.section-primary .section-head h1 { margin: 0; }

.section-primary .section-subhead { padding: 10px 0; margin-bottom: 3px; background-color: #e0e0e0; }
.section-primary .section-subhead h2 { font-size: 11px; line-height: 1.3; font-weight: 500; letter-spacing: 0.02em; color: $black; margin: 0; }
.section-primary .section-subhead h2 small { font-size: 7px; padding-left: 4px; letter-spacing: 0.01em; }

.section-primary .section-entry p { font-size: 11px; padding: 12px 0 0; margin: 0; }

$breakpoint-tablet {
	.section-primary .list-referrals { margin-top: -10px; }

	.section-primary .section-head { padding: 0 0 26px; }

	.section-primary .section-subhead { padding: 12px 0 13px; margin-bottom: 17px; }
	.section-primary .section-subhead h2 { font-size: 15px; }
	.section-primary .section-subhead h2 small { font-size: 11px; padding-left: 11px; }

	.section-primary .section-body ~ .section-body .list-boosts { padding-bottom: 15px; }

	.section-primary .section-entry p { font-size: 15px; padding: 6px 0 0; }
}

/* ------------------------------------------------------------ *\
	Section Secondary
\* ------------------------------------------------------------ */

.section-secondary h2 { font-size: 11px; line-height: 1.3; font-weight: 500; letter-spacing: 0.02em; color: $black; margin: 0; }

.section-secondary .list-funds li { padding: 22px 0 21px 48px; }
.section-secondary .list-funds li em { padding-top: 1px; }

.section-secondary .section-head { padding: 10px 0; background-color: #e0e0e0; }

.section-secondary .section-review { padding: 66px 0 0; }
.section-secondary .section-review .section-head { padding: 0; background-color: transparent; }
.section-secondary .section-review .section-body { padding-top: 42px; }

.section-secondary .section-icon { width: 42px; height: 130px; position: absolute; bottom: 0; right: 0; overflow: hidden; }
.section-secondary .section-icon > i { position: absolute; bottom: 0; right: 0; transform: scale(1.5) rotate(12deg) translate(8px, 1px); }

$breakpoint-tablet {
	.section-secondary h2 { font-size: 15px; }

	.section-secondary .list-statistics { margin-top: 18.7vh; }

	.section-secondary .list-funds li { padding: 15px 0 15px 71px; }
	.section-secondary .list-funds li em { padding-top: 9px; }
	.section-secondary .list-funds li small { line-height: 12px; }
	.section-secondary .list-funds li small.heleum { line-height: 1; }
	.section-secondary .list-funds li:last-child small { position: relative; top: -2px; }

	.section-secondary .section-head { padding: 12px 0 13px; }

	.section-secondary .section-icon { width: 58px; height: 191px; }
	.section-secondary .section-icon > i { top: 0; left: 0; bottom: auto; right: auto; transform: scale(.9) rotate(9deg) translate(-18px, -11px); }

	.section-secondary .section-review { padding-top: 12.85vh; }

	.section-secondary .section-review .section-head i { margin-bottom: 8.55vh; }
	.section-secondary .section-review .section-body { padding-top: .95vh; }
}
