/* ------------------------------------------------------------ *\
	Main
\* ------------------------------------------------------------ */

.main { opacity: 0; }
.main-primary { flex: 0 0 100%; }

.main:not(.main-primary) { min-height: 100%; display: flex; flex-flow: row nowrap; align-items: flex-end; flex: 0 0 100%; }

$breakpoint-tablet {
	.main.main-secondary { min-height: calc(100vh - 118px); display: flex; flex-flow: row nowrap; align-items: flex-end; }
}
