/* ------------------------------------------------------------ *\
	Wrapper
\* ------------------------------------------------------------ */

.wrapper { min-height: calc(100% - 54px); flex: 0 0 100%; display: flex; opacity: 0; position: relative; z-index: 3; padding-top: 54px; transition: opacity .35s ease-in-out .2s; }

$breakpoint-tablet {
	.wrapper { min-height: calc(100% - 118px); padding-top: 118px; }
}
