/* ------------------------------------------------------------ *\
	Balloon
\* ------------------------------------------------------------ */

.balloon { text-align: center; opacity: 0; position: absolute; top: 112px; right: 0; left: 0; margin-bottom: 25px; transform: scale(0.9) rotate(3deg) translate(6px, 15vh); transition: opacity .75s linear .75s, transform 2s ease-in-out 1s; pointer-events: none; }
.balloon ~ .main .container { opacity: 0; transition: opacity .5s ease 1.5s; }

$breakpoint-tablet {
	.balloon { top: 18.5547vh; }
}

$breakpoint-minor {
	.balloon { top: 13vh; }
}
