/* ------------------------------------------------------------ *\
	List
\* ------------------------------------------------------------ */

[class^="list-"] { list-style: none outside none; }

/* ------------------------------------------------------------ *\
	List Radios
\* ------------------------------------------------------------ */

.list-radios { display: flex; flex-flow: row nowrap; margin: 0 -5px; }
.list-radios li { flex: 0 0 calc(100% /3); max-width: calc(100% / 3); padding: 0 5px; margin: 0; }

$breakpoint-tablet {
	.list-radios { margin: 0 -10px; }
	.list-radios li { padding: 0 10px; }
}

/* ------------------------------------------------------------ *\
	List Accounts
\* ------------------------------------------------------------ */

.list-accounts { padding: 0; margin: 0; }
.list-accounts li { padding-bottom: 20px; margin: 0; }
.list-accounts a { font-size: 11px; line-height: 1.1; font-weight: 400; min-height: 27px; display: block; position: relative; padding: 0 20px 0 52px; }
.list-accounts a span { text-align: center; width: 41px; height: 27px; display: block; position: absolute; top: 0; left: 0; padding-top: 6px; background-color: #653394; border-radius: 3px; }
.list-accounts a span small { font-size: 8px; line-height: 13px; color: $white; font-weight: 500; text-align: center; padding: 1px 5px; background-color: color($black a(20%)); border-radius: 6px; }
.list-accounts a strong { font-weight: 400; display: block; padding-bottom: 3px; }
.list-accounts a > small { font-size: 10px; color: $text-faded; font-weight: 300; display: block; }
.list-accounts a i { display: none; position: absolute; top: 0; right: 0; bottom: 0; margin: auto; }

.list-accounts .list-item-favorite a i { display: block; }

.list-accounts .add-account span { background-color: $text-action; }
.list-accounts .add-account span small { width: 11px; height: 11px; background-color: transparent; border-radius: 0; }
.list-accounts .add-account span small:after { transform: rotate(-90deg); }

.list-accounts .add-account span small:before,
.list-accounts .add-account span small:after { content: ''; width: 1px; height: 11px; @extend centered; margin: auto; background-color: $white; }

.list-accounts .add-account strong { color: $text-action; padding-top: 8px; }

$breakpoint-tablet {
	.list-accounts li { padding-bottom: 23px; }
	.list-accounts a { font-size: 12px; min-height: 34px; padding: 0 20px 0 73px; }
	.list-accounts a span { width: 51px; height: 34px; padding-top: 9px; }
	.list-accounts a span small { font-size: 9px; line-height: 14px; min-width: 36px; padding: 4px 9px; border-radius: 10px; }
	.list-accounts a strong { padding: 2px 0 6px; }
	.list-accounts a > small { font-size: 12px; letter-spacing: 0.03em; }

	.list-accounts .add-account strong { font-size: 11px; padding-top: 10px; }
}

/* ------------------------------------------------------------ *\
	List Funds
\* ------------------------------------------------------------ */

.list-funds { padding: 0; border-top: 1px solid #e0e0e0; margin: 0; }
.list-funds li { position: relative; padding: 22px 0 22px 48px; border-bottom: 1px solid #e0e0e0; margin: 0; }

.list-funds em { font-size: 10px; font-weight: 300; font-style: normal; letter-spacing: 0.02em; float: right; padding-top: 2px; }
.list-funds em.funds-deposit { color: #00b828; }
.list-funds em.funds-withdraw { color: #dc0000; }

.list-funds span { text-align: center; width: 41px; height: 27px; text-transform: lowercase; display: block; position: absolute; top: 0; left: 0; bottom: 0; padding-top: 3px; margin: auto; background-color: $text-action; border-radius: 3px; }
.list-funds span small { font-size: 9px; line-height: 12px; color: $white; font-weight: 500; text-align: center; min-width: 29px; display: inline-block; padding: 1px 5px; background-color: color($black a(20%)); border-radius: 6px; }
.list-funds span small sup { font-size: 100%; line-height: .75; }

.list-funds strong { font-size: 12px; line-height: 1.2; font-weight: 400; max-width: 130px; display: block; }
.list-funds strong small { font-size: 100%; color: $text-faded; }

.list-funds li:first-child { padding-top: 14px; padding-bottom: 14px; }
.list-funds li:first-child em { padding-top: 7px; }
.list-funds li:first-child span { font-size: 8px; text-transform: uppercase; padding-top: 7px; background-color: #653394; }

$breakpoint-tablet {
	.list-funds li { padding: 15px 0 15px 72px; }
	.list-funds em { font-size: 12px; padding-top: 8px; }
	.list-funds span { width: 51px; height: 34px; padding-top: 9px; }
	.list-funds span small { font-size: 9px; line-height: 14px; min-width: 36px; position: relative; top: -1px; padding: 4px 9px; border-radius: 10px; }
	.list-funds span small.heleum { line-height: 1; }
	.list-funds strong { font-size: 12px; line-height: 1.25; max-width: none; padding: 0; padding: 9px 0; }
	.list-funds > small { font-size: 12px; letter-spacing: 0.03em; }
}

/* ------------------------------------------------------------ *\
	List Balloons
\* ------------------------------------------------------------ */

.list-balloons { padding: 0; margin: 0; }
.list-balloons li { margin: 0; }
.list-balloons li + li { border-top: 1px dotted transparent; background: url(images/dotted.png) repeat-x 0 0 / auto 1px; }
.list-balloons a { font-size: 11px; line-height: 1.13; display: block; position: relative; padding: 18px 70px 18px 21px; }
.list-balloons a i { position: absolute; top: 0; bottom: 0; left: 0; margin: auto; }
.list-balloons a strong { font-weight: 300; display: block; padding-bottom: 2px; }
.list-balloons a small { font-size: 10px; color: #9da8b1; display: block; }
.list-balloons a span { font-size: 10px; line-height: 11px; color: $white; font-weight: 500; text-transform: uppercase; position: absolute; top: 50%; right: 0; padding: 5px 11px; margin-top: -9px; background-color: $highlight; border-radius: 10px; transition: background-color .35s ease-out; }
.list-balloons a em { font-size: 15px; line-height: 16px; font-style: normal; font-weight: 300; position: absolute; top: 50%; right: 0; margin-top: -7px; }

@media ( min-width: 1025px ) {
	.list-balloons a:hover span { background-color: #b3d900; }
}

$breakpoint-tablet {
	.list-balloons a { font-size: 13px; padding: 23px 65px 23px 30px; }
	.list-balloons a small { font-size: 12px; }
	.list-balloons a strong { padding-bottom: 4px; }
	.list-balloons a span { font-size: 12px; line-height: 14px; padding: 6px 19px 4px; margin-top: -12px; border-radius: 12px; }

	.list-balloons li { border-bottom: 1px dotted transparent; background: url(images/dotted.png) repeat-x 0 100% / auto 1px; }
	.list-balloons li + li { border-top: 0; background: url(images/dotted.png) repeat-x 0 100% / auto 1px; }
}

/* ------------------------------------------------------------ *\
	List Transactions
\* ------------------------------------------------------------ */

.list-transactions { font-size: 11px; padding: 5px 0; margin: 0; }
.list-transactions li { position: relative; padding: 17px 70px 17px 0; margin: 0; }
.list-transactions li + li { border-top: 1px dotted transparent; background: url(images/dotted.png) repeat-x 0 0 / auto 1px; }

.list-transactions p { margin: 0; }
.list-transactions p small { font-size: 10px; color: $text-faded; display: block; }

.list-transactions strong { font-weight: 300; position: absolute; top: 50%; right: 0; margin-top: -5px; }

$breakpoint-tablet {
	.list-transactions { font-size: 15px; padding: 0; }
	.list-transactions li { padding: 18px 100px 19px 0; border-bottom: 1px dotted transparent; background: url(images/dotted.png) repeat-x 0 100% / auto 1px; }
	.list-transactions li + li { border-top: 0; background: url(images/dotted.png) repeat-x 0 100% / auto 1px; }
	.list-transactions strong { margin-top: -11px; }
	.list-transactions p small { font-size: 14px; padding-top: 4px; }

}

/* ------------------------------------------------------------ *\
	List Boosts
\* ------------------------------------------------------------ */

.list-boosts { font-size: 11px; line-height: 1.27; font-weight: 300; padding: 0 0 2px
	; margin: 0; }
.list-boosts p { margin: 0; }
.list-boosts li { display: flex; flex-flow: row nowrap; align-items: center; justify-content: space-between; padding: 15px 0 17px; margin: 0; }
.list-boosts li + li { border-top: 1px dotted transparent; background: url(images/dotted.png) repeat-x 0 0 / auto 1px; }

.list-boosts span,
.list-boosts strong { color: #00b828; }

$breakpoint-tablet {
	.list-boosts { font-size: 15px; padding-bottom: 29px; }
	.list-boosts li { padding: 14px 0; }

	.list-boosts .socials a { margin: 0 8px; }
	.list-boosts .socials a.socials-copy { margin: 0 0 0 14px; }
}

/* ------------------------------------------------------------ *\
	List Referrals
\* ------------------------------------------------------------ */

.list-referrals { font-size: 8px; line-height: 1.5; padding: 0; margin: 0; }
.list-referrals p { margin: 0; }
.list-referrals li { display: flex; flex-flow: row nowrap; align-items: center; justify-content: space-between; padding: 8px 0; border-bottom: 1px dotted transparent; margin: 0; background: url(images/dotted.png) repeat-x 0 100% / auto 1px; }

$breakpoint-tablet {
	.list-referrals { font-size: 12px; }
	.list-referrals li { padding: 23px 0 22px; }
	.list-referrals .btn-secondary { font-size: 10px; line-height: 11px; padding: 5px 9px; }
}

/* ------------------------------------------------------------ *\
	List Balloon Details
\* ------------------------------------------------------------ */

.list-balloon-details { font-size: 12px; line-height: 17px; font-weight: 400; padding: 0 0 21px; border-top: 1px solid #e0e0e0; margin: 0; }
.list-balloon-details li { padding: 11px 0 10px; border-bottom: 1px solid #e0e0e0; margin: 0; overflow: hidden; }
.list-balloon-details li span { font-size: 11px; color: $text-faded; float: right; }
.list-balloon-details li span.mark { color: #00b828; }

$breakpoint-tablet {
	.list-balloon-details li { padding: 23px 0; }
}

/* ------------------------------------------------------------ *\
	List Statistics
\* ------------------------------------------------------------ */

.list-statistics { font-size: 11px; line-height: 1.18; font-weight: 300; padding: 6px 0; margin: 0; }
.list-statistics li { padding: 17px 0 18px; border-bottom: 1px solid #e0e0e0; margin: 0; overflow: hidden; }
.list-statistics li span { float: right; }
.list-statistics li span.mark { color: #00b828; }

$breakpoint-tablet {
	.list-statistics { font-size: 15px; }
	.list-statistics li { padding: 24px 0 25px; }
}
