/* ------------------------------------------------------------ *\
	Header
\* ------------------------------------------------------------ */

.header { position: fixed; top: 0; right: 0; left: 0; z-index: 10; padding: 18px 0; background: $white url(images/temp/waves-mobile.jpg) no-repeat 50% 0 / 100% auto fixed; }

.header .btn-clear { opacity: 1; position: absolute; top: 3px; transform: translateX(0%); transition: opacity .35s ease-in-out, transform .35s ease-in-out; }
.header .btn-clear span { font-size: 11px; font-weight: 400; color: #9da8b1; }
.header .btn-clear span i { margin-top: -2px; }
.header .btn-clear small { font-size: 13px; line-height: 1; color: #9da8b1; display: inline-block; vertical-align: middle; padding-left: 9px; }

.header .btn-clear:first-child,
.header .btn-clear:nth-child(2) { left: 20px; }

.header .btn-clear:last-child { right: 20px; }
.header .btn-clear:last-child i { margin-left: 3px; }

.header .container { width: 100%; max-width: 100%; text-align: center; }

.header .btn-clear.hidden { display: block; opacity: 0; pointer-events: none; }

.header .btn-clear.hidden:first-child,
.header .btn-clear.hidden:nth-child(2) { transform: translateX(-100%); }

.header .btn-clear.hidden:last-child { transform: translateX(100%); }

@media ( max-width: $responsive-sm ) {
	.header .btn-clear small { display: none; }
}

$breakpoint-tablet {
	.header { padding: 44px 0; background-image: url(images/temp/waves.jpg); }
	.header .container { max-width: 1520px; }

	.header .btn-clear { top: 7px; }
	.header .btn-clear span { font-size: 13px; }

	.header .btn-clear:last-child i { margin-left: 13px; }

	.header .btn-clear:nth-child(2) small { letter-spacing: 0.02em; padding-left: 14px; }
}
