/* ------------------------------------------------------------ *\
	Form Elements
\* ------------------------------------------------------------ */

input::placeholder { color: $text-faded; font-weight: 300; opacity: 1; }
textarea::placeholder { color: $text-faded; font-weight: 300; opacity: 1; }

input:-webkit-autofill { -webkit-text-fill-color: $text; -webkit-box-shadow: 0 0 0 1000px $white inset; }

input[type="email"], 
input[type="number"], 
input[type="search"], 
input[type="text"], 
input[type="tel"], 
input[type="url"], 
input[type="password"], 
textarea, 
select { font-family: $font-family-sans-serif; font-size: 16px; line-height: 18px; color: $text; font-weight: 400; width: 100%; height: 40px; display: block; padding: 9px 0; border-width: 1px 0; border-style: solid; border-color: transparent transparent #e0e0e0; margin: 0; background: transparent; border-radius: 0; box-shadow: none; }

input[type="email"]:focus, 
input[type="number"]:focus, 
input[type="search"]:focus, 
input[type="text"]:focus, 
input[type="tel"]:focus, 
input[type="url"]:focus, 
input[type="password"]:focus, 
textarea:focus, 
select:focus { border-width: 1px 0; border-color: transparent transparent #e0e0e0; outline: 0; }

.radio { position: relative; }
.radio input { width: 0; height: 0; opacity: 0; position: absolute; top: 0; left: 0; z-index: -1; -webkit-appearance: none; -moz-appearance: none; appearance: none; clip: rect(0,0,0,0); }
.radio label { font-size: 12px; line-height: 19px; color: $highlight; text-transform: uppercase; text-align: center; font-weight: 600; width: 100%; height: 41px; display: block; padding: 10px; border: 1px solid $text-faded; margin: 0; background-color: $white; border-radius: 5px; transition: all .35s ease-in-out; }

.radio label:before,
.radio label:after { content: ''; @extend centered; left: auto; margin: auto; transition: transform .3s ease; }

.radio label:before { width: 18px; height: 18px; right: 9px; z-index: 1; background-color: $white; border-radius: 50%; transform: scale(0); }
.radio label:after { width: 9px; height: 5px; z-index: 2; right: 13px; border-width: 0 0 2px 2px; border-style: solid; border-color: transparent transparent $highlight $highlight; transform: scale(0) rotate(-45deg); }

.radio input:checked ~ label { text-align: left; color: $white; border-color: $highlight; background-color: $highlight; box-shadow: 0 10px 20px 5px color($black a(30%)); }
.radio input:checked ~ label:before {  transform: scale(1); }
.radio input:checked ~ label:after { transform: scale(1) rotate(-45deg); }

$breakpoint-tablet {
	input[type="email"], 
	input[type="number"], 
	input[type="search"], 
	input[type="text"], 
	input[type="tel"], 
	input[type="url"], 
	input[type="password"], 
	textarea, 
	select { font-size: 14px; line-height: 18px; }

	.radio label { font-size: 14px; line-height: 18px; height: 48px; padding: 14px 10px; }
	.radio label:after { bottom: 2px; }
}
