/* ------------------------------------------------------------ *\
	Socials
\* ------------------------------------------------------------ */

.socials { font-size: 0; line-height: 0; margin: -3px 0; }
.socials a { display: inline-block; vertical-align: middle; }
.socials a i { @extend centered; margin: auto; transform: scale(.8); }

.socials-facebook,
.socials-twitter { width: 29px; height: 29px; position: relative; margin-right: 10px; border-radius: 50%; }

.socials-facebook { background-color: #3b579d; }
.socials-facebook:hover { background-color: #2968ce; }

.socials-twitter { background-color: #00b6f1; }
.socials-twitter:hover { background-color: #03d0ff; }

.socials-copy { font-size: 11px; line-height: 1.73; color: $highlight; font-weight: 400; text-transform: uppercase; text-align: right; min-width: 66px; padding-left: 6px; }

.socials-copy span { color: inherit; }

.socials-copy small { width: 16px; height: 16px; display: inline-block; vertical-align: middle; position: relative; margin: 0 -5px 0 3px; background-color: currentColor; border-radius: 50%; }
.socials-copy small:before { content: ''; width: 8px; height: 5px; display: block; position: absolute; top: 50%; left: 50%; border-width: 0 0 2px 2px; border-style: solid; border-color: transparent transparent $white $white; margin: -3px 0 0 -4px; transform: rotate(-45deg); }

.socials-copy span:last-child,
.socials-copy.clicked span:first-child { display: none; }

.socials-copy.clicked span:last-child { display: inline-block; }

$breakpoint-tablet {
	.socials a i { transform: none; }

	.socials-facebook,
	.socials-twitter { width: 39px; height: 39px; }
	
	.socials-copy { font-size: 15px; min-width: 87px; margin-left: 12px; }
	.socials-copy small { margin-top: -2px; }
}
