/* ------------------------------------------------------------ *\
	Modal
\* ------------------------------------------------------------ */

.modal { opacity: 0; visibility: hidden; position: fixed; top: 0; right: 0; left: 0; bottom: 0; z-index: 11; padding: 56px 20px 58px; background-color: color($black a(10%)); transition: opacity .35s ease, visibility .35s ease; pointer-events: none; }
.modal h2 { font-size: 12px; line-height: 1.5; color: $text-action; font-weight: 400; letter-spacing: 0; margin: 0; }

.modal-head { position: relative; padding: 12px 30px 9px 17px; border-bottom: 1px solid #e0e0e0; background-color: #f7f7f7; border-radius: 5px 5px 0 0; }
.modal-body { padding: 19px; }
.modal-inner { height: 100%; opacity: 0; visibility: hidden; overflow-x: hidden; overflow-y: auto; -webkit-overflow-scrolling: touch; background-color: $white; border-radius: 5px; box-shadow: 0 0 10px color($black a(50%)); transform: translateY(-100%); transition: all .35s ease .35s; }
.modal-close { position: absolute; top: 0; bottom: 0; right: 15px; margin: auto; transform: rotate(45deg); }

.modal-visible { opacity: 1; visibility: visible; pointer-events: auto; }
.modal-visible .modal-inner { opacity: 1; visibility: visible; transform: translateY(0%); }

$breakpoint-tablet {
	.modal { display: flex; }
	.modal h2 { font-size: 14px; }

	.modal-head { padding: 18px 30px 10px 17px; }
	.modal-inner { min-height: 464px; width: 400px; max-height: 100%; height: auto; margin: auto; flex: 0 0 1; align-self: flex-start; }
}
