/* ------------------------------------------------------------ *\
	Tabs
\* ------------------------------------------------------------ */

.tabs { position: relative; }

.tab { height: 0; opacity: 0; visibility: hidden; position: absolute; top: 0; right: 0; left: 0; overflow: hidden; transition: opacity .35s ease-in-out, visibility .35s ease-in-out; }
.tab.current { height: auto; opacity: 1; visibility: visible; position: relative; top: auto; right: auto; left: auto; overflow: visible; }
