/* ------------------------------------------------------------ *\
	Button
\* ------------------------------------------------------------ */

.btn:not(.hidden) { display: inline-block; vertical-align: middle; }

/* ------------------------------------------------------------ *\
	Button Light
\* ------------------------------------------------------------ */

.btn-light { font-size: 16px; line-height: 1.25; color: $highlight; font-weight: 300; letter-spacing: 0; position: relative; width: 320px; height: 88px; padding: 26px 10px 46px 0; border: 0; background: url(images/btn-start.png) no-repeat 0 0; background-size: 100% 100%; }

$breakpoint-tablet {
	.btn-light { width: 376px; height: 103px; padding-top: 27px; }
}

$retina {
	.btn-light { background-image: url(images/btn-start@2x.png); background-size: 100% 100%; }
}

/* ------------------------------------------------------------ *\
	Button Clear
\* ------------------------------------------------------------ */

.btn-clear { line-height: 1; letter-spacing: 0; width: auto; height: auto; padding: 0; border: 0; }

/* ------------------------------------------------------------ *\
	Button Primary
\* ------------------------------------------------------------ */

.btn-primary { font-size: 16px; line-height: 1.25; color: $white; font-weight: 300; letter-spacing: 0; text-align: left; width: auto; height: auto; position: relative; padding: 15px 50px 15px 22px; border: 0; background-color: $highlight; border-radius: 25px; box-shadow: 0 10px 20px 5px color($black a(30%)); }
.btn-primary:before { content: ''; width: 30px; height: 30px; @extend centered; left: auto; right: 11px; z-index: 1; margin: auto; background: url(images/ico-play@2x.png); border-radius: 50%; background-size: 100% 100%; }

.btn-primary:hover { color: $white; }
.btn-primary:hover:before { background-image: url(images/ico-play-hover@2x.png); }

.btn-primary[disabled],
.btn-primary.disabled { background-color: #e0e0e0; box-shadow: none; }

.btn-primary[disabled]:before,
.btn-primary.disabled:before,
.btn-primary[disabled]:hover:before,
.btn-primary.disabled:hover:before { background-image: url(images/ico-play-disabled@2x.png); }

/* ------------------------------------------------------------ *\
	Button Secondary
\* ------------------------------------------------------------ */

.btn-secondary { font-size: 10px; line-height: 11px; color: #f5f5f5; font-weight: 500; text-transform: uppercase; letter-spacing: 0; padding: 5px 9px; border: 0; background-color: #bdc3c7; border-radius: 10px; }
.btn-secondary.current { background-color: $highlight; }
.btn-secondary-active { background-color: #00b828; }

$breakpoint-tablet {
	.btn-secondary { font-size: 12px; line-height: 14px; padding: 5px 14px; border-radius: 12px; }
}

/* ------------------------------------------------------------ *\
	Buttons Hover States
\* ------------------------------------------------------------ */

@media ( min-width: 1025px ) {
	.btn-light:hover { color: $highlight; }
	
	.btn-primary:hover { color: $white; background-color: #b3d900; }
	.btn-primary:hover:after { border-left-color: #b3d900; }

	.btn-secondary.current:hover { background-color: #b3d900; }
	.btn-secondary-active:hover { background-color: #b3d900; }
}
