/* ------------------------------------------------------------ *\
	App Loaded
\* ------------------------------------------------------------ */

.app-loading .splash .btn { opacity: 1; }

.app-loading .splash-logo { opacity: 1; transform: translate(-50%, -55%); animation: rise 10s 1 linear; }
.app-loading .splash-logo-letter { animation: rotate-in 2.5s 1 linear; }

.app-loading .splash-image { transform: scale(1.5); }

.app-loaded .splash { opacity: 0; pointer-events: none; }
.app-loaded .wrapper { opacity: 1; }

.app-loaded .balloon { opacity: 1; transform: scale(0.9) rotate(3deg) translate(6px, 0vh); }
.app-loaded .balloon i { animation: float 10s infinite alternate; animation-delay: 3s; }
.app-loaded .balloon ~ .main .container { opacity: 1; }

.app-hiding .splash-logo { opacity: 0; transform: translate(-50%, -70vh); transition-duration: 1s; }
.app-hiding .splash-logo-letter { animation: rotate-out .9s 1 linear; }

.app-hiding .splash-image { transform: scale(2); transition-duration: 1s; }
